"use client";

import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';

interface Question {
  id: number;
  question: string;
  code?: string;
  options: string[];
  correctAnswer: number;
  explanation: string;
  difficulty: 'easy' | 'medium' | 'hard';
}

const questions: Question[] = [
  {
    id: 1,
    question: "¬øCu√°l es la forma correcta de declarar una variable en JavaScript?",
    options: ["var nombre = 'Juan';", "variable nombre = 'Juan';", "string nombre = 'Juan';", "declare nombre = 'Juan';"],
    correctAnswer: 0,
    explanation: "En JavaScript usamos 'var', 'let' o 'const' para declarar variables. 'var' es la forma tradicional.",
    difficulty: 'easy'
  },
  {
    id: 2,
    question: "¬øQu√© mostrar√° este c√≥digo en la consola?",
    code: `let edad = 25;
console.log("Mi edad es: " + edad);`,
    options: ["Mi edad es: 25", "Mi edad es: edad", "25", "Error"],
    correctAnswer: 0,
    explanation: "El operador + concatena el texto con el valor de la variable edad (25).",
    difficulty: 'easy'
  },
  {
    id: 3,
    question: "¬øCu√°l es el tipo de dato de la variable 'esEstudiante'?",
    code: `let esEstudiante = true;`,
    options: ["string", "number", "boolean", "object"],
    correctAnswer: 2,
    explanation: "true y false son valores booleanos en JavaScript.",
    difficulty: 'easy'
  },
  {
    id: 4,
    question: "¬øQu√© resultado dar√° esta funci√≥n?",
    code: `function sumar(a, b) {
  return a + b;
}
console.log(sumar(5, 3));`,
    options: ["8", "53", "Error", "undefined"],
    correctAnswer: 0,
    explanation: "La funci√≥n suma los n√∫meros 5 + 3 = 8.",
    difficulty: 'medium'
  },
  {
    id: 5,
    question: "¬øC√≥mo accedemos al primer elemento de un array?",
    code: `let colores = ["rojo", "verde", "azul"];`,
    options: ["colores[1]", "colores[0]", "colores.first()", "colores.get(0)"],
    correctAnswer: 1,
    explanation: "Los arrays en JavaScript empiezan en √≠ndice 0, por lo que el primer elemento es [0].",
    difficulty: 'medium'
  },
  {
    id: 6,
    question: "¬øQu√© mostrar√° este c√≥digo?",
    code: `let numero = 15;
if (numero > 10) {
  console.log("Mayor");
} else {
  console.log("Menor");
}`,
    options: ["Mayor", "Menor", "15", "Error"],
    correctAnswer: 0,
    explanation: "Como 15 > 10 es verdadero, se ejecuta el primer bloque y muestra 'Mayor'.",
    difficulty: 'medium'
  },
  {
    id: 7,
    question: "¬øCu√°l es la diferencia entre '==' y '==='?",
    options: [
      "No hay diferencia",
      "=== compara valor y tipo, == solo valor",
      "== compara valor y tipo, === solo valor",
      "=== es para n√∫meros, == para texto"
    ],
    correctAnswer: 1,
    explanation: "=== es comparaci√≥n estricta (valor y tipo), == es comparaci√≥n con conversi√≥n de tipo.",
    difficulty: 'hard'
  },
  {
    id: 8,
    question: "¬øQu√© mostrar√° este bucle?",
    code: `for (let i = 0; i < 3; i++) {
  console.log(i);
}`,
    options: ["0 1 2", "1 2 3", "0 1 2 3", "Error"],
    correctAnswer: 0,
    explanation: "El bucle empieza en 0, contin√∫a mientras i < 3, mostrando 0, 1, 2.",
    difficulty: 'hard'
  }
];

export default function QuizGame() {
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [score, setScore] = useState(0);
  const [lives, setLives] = useState(3);
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null);
  const [showExplanation, setShowExplanation] = useState(false);
  const [gameOver, setGameOver] = useState(false);
  const [gameWon, setGameWon] = useState(false);

  const currentQ = questions[currentQuestion];
  const progress = ((currentQuestion + 1) / questions.length) * 100;

  const handleAnswerSelect = (answerIndex: number) => {
    if (showExplanation) return;
    setSelectedAnswer(answerIndex);
  };

  const handleSubmitAnswer = () => {
    if (selectedAnswer === null) return;

    setShowExplanation(true);

    if (selectedAnswer === currentQ.correctAnswer) {
      setScore(score + (currentQ.difficulty === 'easy' ? 10 : currentQ.difficulty === 'medium' ? 20 : 30));
    } else {
      setLives(lives - 1);
      if (lives - 1 <= 0) {
        setGameOver(true);
        return;
      }
    }
  };

  const handleNextQuestion = () => {
    if (currentQuestion + 1 >= questions.length) {
      setGameWon(true);
      return;
    }

    setCurrentQuestion(currentQuestion + 1);
    setSelectedAnswer(null);
    setShowExplanation(false);
  };

  const resetGame = () => {
    setCurrentQuestion(0);
    setScore(0);
    setLives(3);
    setSelectedAnswer(null);
    setShowExplanation(false);
    setGameOver(false);
    setGameWon(false);
  };

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'text-green-400';
      case 'medium': return 'text-yellow-400';
      case 'hard': return 'text-red-400';
      default: return 'text-gray-400';
    }
  };

  const getDifficultyBg = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'bg-green-600';
      case 'medium': return 'bg-yellow-600';
      case 'hard': return 'bg-red-600';
      default: return 'bg-gray-600';
    }
  };

  if (gameOver) {
    return (
      <Card className="bg-gray-800 border-gray-700">
        <CardContent className="text-center py-12">
          <div className="text-6xl mb-4">üíÄ</div>
          <h2 className="text-3xl font-bold text-red-400 mb-4">¬°Game Over!</h2>
          <p className="text-gray-300 mb-2">Te quedaste sin vidas</p>
          <p className="text-xl text-white mb-6">Puntuaci√≥n final: <span className="text-yellow-400 font-bold">{score}</span></p>
          <Button onClick={resetGame} className="bg-blue-600 hover:bg-blue-700">
            üîÑ Jugar de Nuevo
          </Button>
        </CardContent>
      </Card>
    );
  }

  if (gameWon) {
    return (
      <Card className="bg-gray-800 border-gray-700">
        <CardContent className="text-center py-12">
          <div className="text-6xl mb-4">üèÜ</div>
          <h2 className="text-3xl font-bold text-green-400 mb-4">¬°Felicitaciones!</h2>
          <p className="text-gray-300 mb-2">¬°Completaste todas las preguntas!</p>
          <p className="text-xl text-white mb-2">Puntuaci√≥n final: <span className="text-yellow-400 font-bold">{score}</span></p>
          <p className="text-lg text-white mb-6">Vidas restantes: <span className="text-red-400">{lives}</span></p>
          <Button onClick={resetGame} className="bg-blue-600 hover:bg-blue-700">
            üîÑ Jugar de Nuevo
          </Button>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="space-y-6">
      {/* Game Stats */}
      <div className="flex justify-between items-center bg-gray-900 p-4 rounded-lg border border-gray-700">
        <div className="flex items-center gap-4">
          <div className="text-white">
            <span className="text-sm text-gray-400">Puntuaci√≥n:</span>
            <div className="text-2xl font-bold text-yellow-400">{score}</div>
          </div>
          <div className="text-white">
            <span className="text-sm text-gray-400">Vidas:</span>
            <div className="text-2xl">
              {Array.from({ length: 3 }, (_, i) => (
                <span key={i} className={i < lives ? "text-red-500" : "text-gray-600"}>
                  ‚ù§Ô∏è
                </span>
              ))}
            </div>
          </div>
        </div>
        <div className="text-right">
          <div className="text-sm text-gray-400">Pregunta {currentQuestion + 1} de {questions.length}</div>
          <Progress value={progress} className="w-32 mt-1" />
        </div>
      </div>

      {/* Question Card */}
      <Card className="bg-gray-800 border-gray-700">
        <CardHeader>
          <div className="flex justify-between items-start">
            <CardTitle className="text-2xl text-white">{currentQ.question}</CardTitle>
            <span className={`px-3 py-1 rounded-full text-sm font-medium ${getDifficultyBg(currentQ.difficulty)} text-white`}>
              {currentQ.difficulty === 'easy' ? 'F√°cil' : currentQ.difficulty === 'medium' ? 'Medio' : 'Dif√≠cil'}
            </span>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Code Block */}
          {currentQ.code && (
            <div className="bg-black p-4 rounded-lg border border-gray-600">
              <pre className="text-green-400 text-sm overflow-x-auto">
                <code>{currentQ.code}</code>
              </pre>
            </div>
          )}

          {/* Answer Options */}
          <div className="grid gap-3">
            {currentQ.options.map((option, index) => {
              let buttonClass = "w-full text-left p-4 rounded-lg border transition-all ";
              
              if (showExplanation) {
                if (index === currentQ.correctAnswer) {
                  buttonClass += "bg-green-900/50 border-green-500 text-green-200";
                } else if (index === selectedAnswer && index !== currentQ.correctAnswer) {
                  buttonClass += "bg-red-900/50 border-red-500 text-red-200";
                } else {
                  buttonClass += "bg-gray-700 border-gray-600 text-gray-300";
                }
              } else {
                if (selectedAnswer === index) {
                  buttonClass += "bg-blue-900/50 border-blue-500 text-blue-200";
                } else {
                  buttonClass += "bg-gray-700 border-gray-600 text-gray-300 hover:bg-gray-600 hover:border-gray-500";
                }
              }

              return (
                <button
                  key={index}
                  onClick={() => handleAnswerSelect(index)}
                  className={buttonClass}
                  disabled={showExplanation}
                >
                  <span className="font-medium mr-2">
                    {String.fromCharCode(65 + index)}.
                  </span>
                  {option}
                </button>
              );
            })}
          </div>

          {/* Explanation */}
          {showExplanation && (
            <div className="bg-blue-900/30 border border-blue-700 p-4 rounded-lg">
              <h4 className="font-semibold text-blue-200 mb-2">üí° Explicaci√≥n:</h4>
              <p className="text-blue-100">{currentQ.explanation}</p>
            </div>
          )}

          {/* Action Buttons */}
          <div className="flex gap-3 pt-4">
            {!showExplanation ? (
              <Button 
                onClick={handleSubmitAnswer}
                disabled={selectedAnswer === null}
                className="bg-blue-600 hover:bg-blue-700 disabled:bg-gray-600"
              >
                ‚úÖ Confirmar Respuesta
              </Button>
            ) : (
              <Button 
                onClick={handleNextQuestion}
                className="bg-green-600 hover:bg-green-700"
              >
                {currentQuestion + 1 >= questions.length ? 'üèÅ Finalizar' : '‚û°Ô∏è Siguiente Pregunta'}
              </Button>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
